import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalConfig, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import * as _ from 'underscore';
import { GeneralService } from '../../../../../../services/general.service';
import { HeaderService } from '../../../../../../services/header.service';
import { TreasuryService } from '../../services/treasury.service';
import { ModalExpenseDetailsComponent } from '../modal-expense-details/modal-expense-details.component';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';
import { ConfirmModalComponent } from '../../../../../../shared/components/confirm-modal/confirm-modal.component';
import { PettyCashService } from '../../../../cashier/petty-cash/services/petty-cash.service';
@Component({
  selector: 'app-last-expense',
  templateUrl: './last-expense.component.html',
  styleUrls: ['./last-expense.component.scss'],
})
export class LastExpenseComponent implements OnInit {
  @Input() registerId: number;

  expenses: any[] = [];
  totalRecords!: number;
  totalPages!: number;
  items = [1, 2, 1, 2, 1, 2];

  searchText: string = '';
  status: string = '';
  sort: number = 1;
  pageNo: number = 1;
  statusList: any;
  checkedItems;
  action;
  reasonModelRef: NgbModalRef;
  @Output() sendtoLoadData = new EventEmitter();
  appForm: FormGroup;
  replenishmentRequestID = 0;
  constructor(
    private _pettCashService: PettyCashService,
    private treasuryService: TreasuryService,
    public generalService: GeneralService,
    public headerService: HeaderService,
    private modalService: NgbModal,
    private router: Router,
    private _activeRoute: ActivatedRoute,
    config: NgbModalConfig,
    private fb: FormBuilder,
    private toastr: ToastrService,
  ) {
    config.backdrop = 'static';
    config.keyboard = false;
  }

  ngOnInit(): void {
    let params = this._activeRoute.snapshot.params;
    console.log(params);
    this.replenishmentRequestID = params['id2'];
    this.appForm = this.fb.group({
      rejectReason: ['', [Validators.required]]
    })
    if (this.registerId) {
      console.log(this.registerId);

      this.GetMainData();
    }
  }
  onScroll() {
    this.pageNo += 1;
    this.GetMainData();
  }
  GetMainData() {
    //let params = `?status=${this.status}&sort=${this.sort}&pageNo=${this.pageNo - 1}&pageSize=12&registerId=${this.registerId}`;
    let params = `?registerId=${this.registerId}&replenishmentRequestID=${this.replenishmentRequestID}&pageNo=${this.pageNo - 1}&pageSize=1000`
    this._pettCashService.getPettyCashRequestExpensesList(params).subscribe((response: any) => {
      if (response?.data?.data?.length) {
        let expenseList = response.data.data.filter(x => x.status == 2003); //Pending Only
        //Approved Only
        let approvedExpenseAmount = response.data.data.filter(x => x.status == 2004).reduce((acc, cur) => {
          return acc += cur?.expenseTotalAmount
        }, 0)
        this.emitData({ approvedExpenseAmount });
        if (expenseList.length > 0) {
          expenseList = [...this.expenses, ...expenseList];
          this.expenses = expenseList.map((x: any) => {
            return { ...x, checked: false }
          });
          this.totalRecords = response?.data?.totalRecordCount;
        }
      }
    });
  }
  emitData(data) {
    this.sendtoLoadData.emit(data);
  }
  handleCheckBoxChange() {
    this.checkedItems = this.expenses.filter((x: any) => x.checked);
  }
  handleCheckAll(event) {
    let changedArr = [];
    if (event.target.checked) {
      changedArr = this.expenses.map(x => {
        if (x.status == 2003) {
          return { ...x, checked: true }
        }
        else {
          return { ...x, checked: false }
        }
      })
      this.expenses = changedArr
      this.checkedItems = changedArr.filter(x => x.status == 2003);
    }
    else {
      changedArr = this.expenses.map(x => {
        return { ...x, checked: false }
      })
      this.expenses = changedArr;
      this.checkedItems = [];
    }
  }
  Viewdetails(item) {
    this.router.navigateByUrl('/petty-cash/request-details/1');
    this.headerService.setTitle(
      'Petty Cash Monitoring > Petty Cash Replenishment Request > View Details'
    );
  }
  viewExpense() {
    this.headerService.setTitle('petty-cash > View role');
  }
  confirmModal(action, content) {
    this.appForm.reset();
    this.action = action;
    const modelRef = this.modalService.open(ConfirmModalComponent);
    modelRef.componentInstance.data = { body: `Are you sure you want to ${action} expense` };
    modelRef.componentInstance.eventData.subscribe(value => {
      if (value) {
        this.handleMultipleExpense(content);
      }
    })
  }
  handleMultipleExpense(content) {
    this.save();
    if (this.action !== 'approve') {
      this.reasonModelRef = this.modalService.open(content, { centered: true, size: 'md' });
    }
  }
  save() {
    let ids = this.checkedItems.map(x => x.expenseRecordId).toString();
    const formData = new FormData();
    formData.append('ExpenseRecordId', ids);
    if (this.action == 'approve') {
      this.treasuryService.ApproveExpense(formData).subscribe(response => {
        console.log(response);
        if (response.data) {
          this.checkedItems = [];
          this.expenses = [];
          this.pageNo = 1;
          this.GetMainData();
          this.toastr.success('Expense approved successfully!', 'Success')
          this.checkedItems = [];

        }
      }, error => {
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'error';
        modalRef.componentInstance.messageError = error?.error?.errors;
      })
    }
    else if (this.action == 'revise') {
      if (!this.appForm.valid)
        return
      formData.append('Notes', this.appForm.get('rejectReason').value);
      this.treasuryService.requestReviseExpense(formData).subscribe(response => {
        if (response.data) {
          this.checkedItems = [];
          this.expenses = [];
          this.pageNo = 1;
          this.GetMainData();
          this.toastr.success('Expense revised successfully!', 'Success');
          this.reasonModelRef.dismiss();
          this.checkedItems = [];

        }
      }, error => {
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'error';
        modalRef.componentInstance.messageError = error?.error?.errors;
      })
    }
    else {
      if (this.appForm.valid) {
        formData.append('RejectionReason', this.appForm.get('rejectReason').value);
        this.treasuryService.rejectMultipleExpense(formData).subscribe(response => {
          console.log(response);
          if (response.data) {
            this.checkedItems = [];
            this.expenses = [];
            this.pageNo = 1;
            this.GetMainData();
            this.toastr.success('Expense rejected successfully!', 'Success')
            this.reasonModelRef.dismiss();
            this.checkedItems = [];

          }
        }, error => {
          const modalRef = this.modalService.open(ModalMessageComponent);
          modalRef.componentInstance.type = 'error';
          modalRef.componentInstance.messageError = error?.error?.errors;
        })
      }
    }
  }

  searchTable(event: any) {
    console.log(event?.target.value);
    const text = event.target.value;
    console.log(text.length);
    if (text.length >= 2) {
      this.searchText = text;
      this.pageNo = 1;
      this.GetMainData();
    }
    if (text.length == 0) {
      this.GetMainData();
    }
  }
  removeSearch() {
    this.searchText = '';
    this.sort = 1;
    this.GetMainData();
  }

  sortByInvoice() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.GetMainData();
  }
  sortByCategory() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.GetMainData();
  }

  sortByIDate() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.GetMainData();
  }
  sortByEmployee() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.GetMainData();
  }

  sortByTotalAmount() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.GetMainData();
  }

  // sortByAmount() {
  //   if (this.sort == 11) {
  //     this.sort = 1;
  //   } else {
  //     this.sort = this.sort == 10 ? 11 : 10;
  //   }
  //   this.GetMainData();
  // }
  sortByStatus() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.GetMainData();
  }

  pageChange(page: number) {
    this.pageNo = page;
    this.GetMainData();
    window.scroll(0, 0);
  }

  filterByStatus(event) {
    console.log(event);
    this.pageNo = 1;
    this.status = event.id;
    this.GetMainData();
  }
  clearStatus(event) {
    console.log(event);
    this.status = '';
    this.GetMainData();
  }

  openModalExpense(item) {
    this.checkedItems = [];
    const modalRef = this.modalService.open(ModalExpenseDetailsComponent, {
      size: 'lg',
    });
    modalRef.componentInstance.expenseDetails = item;
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      console.log('result', result);
      if (result) {
        this.pageNo = 1;
        this.expenses = [];
        this.GetMainData();
      }
    });
  }
}
