import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TreasuryService } from '../../../services/treasury.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { min } from 'rxjs-compat/operator/min';
@Component({
  selector: 'app-modal-edit-setup',
  templateUrl: './modal-edit-setup.component.html',
  styleUrls: ['./modal-edit-setup.component.scss'],
})
export class ModalEditSetupComponent {
  @Input() registerDetails: any;
  @Input() type: any;
  message: string;
  messageError: string;
  pettyCashLimit: any;
  thresholdPercentage = 0;
  msgError: string;
  formGroup: FormGroup;
  @Output() sendtoLoadData = new EventEmitter();

  constructor(
    private treasuryService: TreasuryService,
    private router: Router,
    private route: ActivatedRoute,
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private fb: FormBuilder,
  ) { }

  ngOnInit(): void {
    if (this.registerDetails) {
      this.pettyCashLimit = this.registerDetails.pettyCashLimit;
      this.thresholdPercentage = this.registerDetails.thresholdPercentage;
    }
    this.initForm();
  }
  initForm() {
    this.formGroup = this.fb.group({
      pettycashLimit: [this.registerDetails.pettyCashLimit, [Validators.required]],
      thresholdPercentage: [this.registerDetails?.thresholdPercentage || 0, [Validators.required, Validators.max(100)]],
    })
  }
  EditPettyCashLimit() {

    if (this.formGroup.invalid) {
      this.formGroup.markAllAsTouched();
      return
    }
    const formData = new FormData();
    let values = this.formGroup.value;
    formData.append('registerId', this.registerDetails.id);
    formData.append('PettyCashLimit', values?.pettycashLimit);
    formData.append('thresholdPercentage', values?.thresholdPercentage);
    this.treasuryService
      .EditPettyCashLimit(formData).subscribe((response: any) => {
        if (response.isSuccess == true) {
          this.message = 'Approved successfully ';
          setTimeout(() => {
            this.modalService.dismissAll();
            this.sendtoLoadData.emit();
          }, 3000);
        } else {
          console.log('error', response);
          this.messageError = response.Errors[0]?.ErrorMessageEn;
          setTimeout(() => {
            this.messageError = '';
            // this.modalService.dismissAll();
          }, 3000);
        }
      });
  }
}
